.project {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.project h2 {
    text-align: center;
    text-transform: capitalize;
}
.projectsList {
    width: 80%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
    margin-bottom: 30px;
}
@media only screen and (max-width:1300px) {
    .projectsList {
        grid-template-columns: 1fr 1fr;
    }
}
@media only screen and (max-width: 800px) {
    .projectsList {
      grid-template-columns: 1fr;
    }
  }
  @media only screen and (max-width: 800px) {
    .projectsList {
      width: 100%;
    }
    .projectItem {
      width: 300px;
      height: 300px;
    }
  }
  @media only screen and (max-width: 400px) {
    .projectItem {
      width: 250px !important;
      height: 250px !important;
      margin: 30px 0 !important;
    }
    .projectItem h3 {
        margin-top: 5px !important;
    }
  }
/* Project Items */
.projectItem {
    width: 300px;
    height: 300px;
    margin: 40px;
    text-align: center;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    border-radius: 15px;
}
.projectItem:hover {
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
    transition: 0.3s ease-in;
    cursor: pointer;
  }
.projectItem .bgImage{
    width: 100%;
    height: 200px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.projectItem h3 {
    margin-top: 20px;
    color: #3e497a;
}
/* Projet Display*/
.projectDisplay {
  width: 800px;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto; 
  text-transform: capitalize;
  text-align: center;
  padding: 40px 0 30px;
}
@media screen and (max-width:800px) {
  .projectDisplay {
    width: 100%;
    margin: 0;
  }
}
.projectDisplay img {
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px 0;
}
.projectDisplay a{
  background-color: black;
  color: white;
  padding: 5px 15px;
  border-radius: 30px;
  cursor: pointer;
  margin-bottom: 10px;
}