.header {
    position: sticky;
    top:0;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    z-index: 1000; 
    cursor: pointer;
    background-color: black;
    text-transform: capitalize;
}
.nav-menu {
    display: flex;
}
.header li {
    padding: 0 1.3rem;
}
.header li:hover {
    transform: scale(1.1);
    background-color: var(--main-color);
    border-radius: 5px;
    transition: 0.3s;
}
.nav-menu a {
    font-size: 1.2rem;
    font-weight: 500;
}
.nav-bar {
    display: none;
}
@media screen and (max-width:992px) {
    .nav-bar {
        display: flex;
        margin-right: auto;
    }
    .nav-menu {
        flex-direction: column;
        width: 100%;
        height: 100vh;
        position: fixed;
        justify-content: center;
        align-items: center;
        top: 10px;
        left: -100%;
        text-align: center;
        z-index: -1;
        transition: 0.5s;
        background-color: black;
    }
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li {
        padding: 0.8rem 1.4rem;   
        border-radius: 5px;
    }
    .nav-menu a {
        font-size: 2rem;
    }
}

