.contactForm {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;    
    background-color: black;
}
.inputBox {
     position: relative;
     width: 500px;
     padding: 40px;
}
@media screen and (max-width:500px) {
    .inputBox {
        width: 400px;
        padding: 40px 0;
    }
}
@media screen and (max-width:400px) {
    .inputBox {
        width: 300px;
        padding: 40px 0;
    }
}
@media screen and (max-width:300px) {
    .inputBox {
        width: 200px;
        padding: 40px 0;
    }
}
.inputBox input,
.inputBox textarea{
    width: 100%;
    outline: none;
    background-color: transparent;
    color: white;
    padding: 10px;
    border: 1px solid white;
    border-radius: 5px;
}
.inputBox textarea {
    height: 100px;
}
.inputBox span{
    position: absolute;
    left: 40px;
    color: white;
    padding: 2px 10px;
    pointer-events: none;
    text-transform: uppercase;
    transition: 0.5s;
}
.inputBox input:valid ~ span,
.inputBox input:focus ~ span,
.inputBox textarea:valid ~ span,
.inputBox textarea:focus ~ span
 {
    color: var(--main-color);
    transform: translateX(10px) translateY(-15px);
    background-color: black;
    border-left: 1px solid white;
    border-right: 1px solid white;
    font-size: 0.7rem;
}
.ButtonSend {
    text-align: center;
    padding-bottom: 20px;
}
.ButtonSend input[type='submit'] {
    background-color: transparent;
    padding: 10px 25px;
    color: white;
    outline: none;
    border: 1px solid white;
    border-radius: 30px;
}
.msgSend {
    color: var(--main-color);
    text-align: center;
    width: 300px;
    margin: auto;
    padding-bottom: 10px;
}
@media screen and (max-width:300px) {
    .msgSend {
        width: 200px;
    }
}